.routine {
	height: 735px;
	position: relative;
	@media(max-width: 600px) {
		height: 725px; }
	& .routine__cloud {
		position: absolute;
		top: (-1300%/735);
		left: (-1500%/1440);
		z-index: 1;
		@media(max-width: 600px) {
			width: 344px;
			height: 316px;
			top: 25px;
			left: (12800%/600); }
		@media(max-width: 500px) {
			left: 13.333%; }
		@media(max-width: 400px) {
			top: -4px;
			left: 19px; }
		& .cloud__img {
			@media(max-width: 600px) {
				max-width: 100%; } } }
	& .routine__cloud--375 {
		display: none;
		width: 185px;
		height: 125px;
		@media(max-width: 400px) {
			display: block;
			position: absolute;
			top: 525px;
			left: 291px; }
		& .cloud--375__img {
			max-width: 100%; } }
	& .routine__fix-container {
		display: flex;
		justify-content: flex-end;
		& .routine__video-clip {
			width: 515px;
			height: 317px;
			position: absolute;
			top: 160px;
			left: 127px;
			z-index: 2;
			@media(max-width: 600px) {
				width: 240px;
				top: 105px;
				left: (18600%/600); }
			@media(max-width: 500px) {
				left: 25%; }
			@media(max-width: 400px) {
				top: 86px;
				left: 64px; }
			& video {
				max-width: 100%;
				max-height: 100%; }
			& .video-controller {
				position: relative;
				width: 100%;
				height: 35px;
				background-color: #0009;
				display: flex;
				align-items: center;
				z-index: 3;
				& .video-controller__btn {
					width: 20px;
					height: 20px;
					background-position: center;
					background-repeat: no-repeat;
					background-size: cover;
					background-color: transparent;
					border: none;
					padding: 0;
					margin: 0 0 0 6px; }
				& .video-controller--play {
					background-image: url('../../img/play.png'); }
				& .video-controller--pause {
					background-image: url('../../img/pause.png');
					display: none; }
				& .video-controller__bar {
					position: relative;
					width: 90%;
					height: 1px;
					background-color: rgba(255,255,255, 0.2);
					margin: 0 0 0 14px;
					& .bar__line {
						position: absolute;
						top: -8%;
						left: 0;
						width: 30%;
						height: 100%;
						background-color: #fff; } } } }
		& .routine__text-block {
			width: 610px;
			margin: 115px 0 0 69px;
			@media(max-width: 600px) {
				text-align: center;
				margin: 370px 0 0 0; }
			@media(max-width: 400px) {
				margin: 396px 0 0 0; }
			& h1 {
				margin: 0 0 44px 0;
				font-size: 36px;
				font-weight: 400;
				text-transform: uppercase;
				@media(max-width: 600px) {
					font-size: 16px;
					margin: 0 0 34px 0; } }
			& ol {
				padding: 0 0 0 25px;
				margin: 0 0 0 0;
				line-height: 31.45px;
				list-style-type: none;
				@media(max-width: 600px) {
					text-align: left;
					padding: 0 0 0 80px;
					line-height: 16.45px; }
				@media(max-width: 400px) {
					text-align: left;
					padding: 0; } }
			& li {
				font-size: 22px;
				font-weight: 400;
				@media(max-width: 600px) {
					font-size: 12px; } } } } }
